import React from 'react'
import classNames from 'classnames';

import s from './modal.module.css'

export const Modal = ( {active, setActive, children} ) => {
    return (
        <div className = {classNames(s.modal, active ? s.active : null)} onClick = {() => {setActive(false)}}>
            <div className = {classNames(s.modal__content, active ? s.active : null)} onClick = {e => e.stopPropagation()}>
                {children}
            </div>            
        </div>
    )
}
